
































































import { Component, Prop, Vue, Watch, Emit } from "vue-property-decorator";
import VueSlider from "vue-slider-component";

@Component({
  components: {
    "vue-slider": VueSlider
  }
})
export default class Album extends Vue {
  @Prop({ default: "Title" }) private title!: string;
  @Prop({ default: "Singer" }) private singer!: string;
  @Prop() private image!: string;
  @Prop() private song!: string;
  private localMode = 0;
  private currentTime = 0;
  private duration = 0;
  private paused = true;
  private audio!: HTMLAudioElement;
  private soundValue = 20;
  private options = {
    railStyle: {
      backgroundColor: "#fff",
      border: "3px solid",
      borderRadius: "75px"
    },
    processStyle: { backgroundColor: "#C5C5C5" },
    tooltip: "none",
    duration: 0.3
  };

  mounted() {
    this.audio = new Audio(this.song);
    this.audio.volume = this.soundValue / 100;
    this.audio.addEventListener("loadeddata", () => {
      this.duration = this.audio.duration;
    });
    this.audio.addEventListener("timeupdate", () => {
      this.currentTime = this.audio.currentTime;
    });
  }

  @Watch("song")
  private updateAudio() {
    this.audio.pause();
    this.audio = new Audio(this.song);
    this.audio.currentTime = 0;
    this.audio.volume = this.soundValue / 100;
    this.currentTime = 0;
    if (!this.paused) {
      this.audio.play();
    }
    this.audio.addEventListener("loadeddata", () => {
      this.duration = this.audio.duration;
    });
    this.audio.addEventListener("timeupdate", () => {
      this.currentTime = this.audio.currentTime;
    });
  }

  private formatTime(sec: number) {
    const totalSeconds = Math.floor(sec);
    let mins = Math.floor(totalSeconds / 60).toString();
    let seconds = (totalSeconds % 60).toString();
    if (mins.length < 2) {
      mins = `0${mins}`;
    }
    if (seconds.length < 2) {
      seconds = `0${seconds}`;
    }
    return `${mins}:${seconds}`;
  }

  private pauseMusic() {
    this.audio.pause();
    this.paused = true;
  }

  private playMusic() {
    this.audio.play();
    this.paused = false;
  }

  get timeNow() {
    return this.formatTime(this.currentTime);
  }
  get timeTotal() {
    return this.formatTime(this.duration);
  }

  get timeValue(): number {
    return (this.currentTime / this.duration) * 100;
  }
  set timeValue(value) {
    const nowSeconds = (value / 100) * this.audio.duration;
    this.audio.currentTime = nowSeconds;
  }

  @Watch("currentTime")
  private playEnd() {
    if (this.currentTime === this.audio.duration && this.localMode === 2) {
      this.paused = true;
    } else if(this.currentTime === this.audio.duration && this.localMode === 0){
      this.onNextClick();
    }
  }

  @Watch("soundValue")
  private changeVolume() {
    this.audio.volume = this.soundValue / 100;
  }

  @Emit("nextClick")
  private onNextClick() {
    return;
  }

  @Emit("lastClick")
  private onLastClick() {
    return;
  }

  private onCycleClick() {
    if (this.localMode == 0 || this.localMode == 1) {
      this.localMode += 1;
    } else {
      this.localMode = 0;
    }

    if (this.localMode == 1) {
      this.audio.loop = true;
    } else {
      this.audio.loop = false;
    }
    return this.localMode;
  }
}
